@media (min-width: 250px) {
  .flexParent {
    display: flex;
    flex: 1;
    flex-direction: row;
    height: 90%;
  }
}

.mainContent {
  flex: 1;
  /* overflow-y: scroll; */
  /* overflow: hidden; */
  background-color: transparent;
  
}

.mainContent::-webkit-scrollbar {
  display: none;
}

.spinnerClass {
  text-align: center;
  display: flex;
  position: fixed;

}

.song {
  display: inline-block;
  vertical-align: top;
  text-align: center;
  margin: 10px;
  /* border: 1px dashed green; */
}

.song > span {
  display: inline-block;
}


.buttonInvisible {
  display: none;
}

.buttonVisible {
  display: block;
}

.unmute {
  background-image: url(./icons/unmute.svg);
  background-size: cover;
  width: 50px;
  height: 50px;
  cursor: pointer;
  position: static;

  position: fixed;
  left: 15px;
  top: 15px;
}

.mute {
  background-image: url(./icons/mute.svg);
  background-size: cover;
  width: 50px;
  height: 50px;
  cursor: pointer;

  position: fixed;
  left: 15px;
  top: 15px;
}


body {
  background-color: rgb(8, 8, 8);
  font-family: 'Gilroy-Bold ☞';
  color: white;

  height: 100%;

  display: flex;
  overflow-x: hidden;
  
}

body::-webkit-scrollbar {
  display: none;
}

.nav {
  float: right;
}

#navbar {
  position: fixed;
  display: flex;
  overflow: visible;
  /* flex-basis: 20%; */

  height: 100%;
  /* float: right; */
  /* flex-direction: inherit; */
  right: 0px;
  top: 0px;

  background-color: transparent;
  background: url(./icons/sidebar.svg) no-repeat center center fixed; 

  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.hidden {
  width: 0%;
  min-width: none;
  transition-property: width;
  transition-timing-function: ease-in-out;
  transition-duration: 0.5s;
}

.nothidden {
  width: 20%;
  /* min-width: 200px; */

  transition-property: width;
  transition-timing-function: ease-in-out;
  transition-duration: 0.5s;
}




#navbarContent {
  position: relative;
  top: 5%;
  width: 100%;
  text-align: center;
  margin-top: 30px;
  margin-left: 35px;
  

  

}

#navbarContent::-webkit-scrollbar {
  display: none;
}



