.drophere {
  width: 200px;
  height: 500px;
  padding: 70px 10px;
  left: 100px;
  text-align: center;
  font-family: 'Gilroy-Bold ☞';
  font-size: 20px;
  background-color: transparent;
  color: white;
  border-style: none;

  overflow-x: hidden;
  overflow-y: visible;

  transition-property: background-color;
  transition-timing-function: ease-in-out;
  transition-duration: 0.5s;
}

.drophere::-webkit-scrollbar {
  display: none;
}
.drophere:hover {
  background-color: rgba(76, 175, 79, 0.466);
}